/* ==========================================================================
   Variable: Typography
   ========================================================================== */

$font-main: "Mulish", sans-serif;
$font-icon: "Trafalgar Icon";

// Font
$font: (
  family-main: $font-main,
  family-icon: $font-icon,
  f48: (
    font-size: 4.8rem,
    line-height: 5.6rem,
  ),
  f36: (
    font-size: 3.6rem,
    line-height: 4.8rem,
  ),
  f24: (
    font-size: 2.4rem,
    line-height: 3rem,
  ),
  f22: (
    font-size: 2.2rem,
    line-height: 3.2rem,
  ),
  f20: (
    font-size: 2rem,
    line-height: 3rem,
  ),
  f18: (
    font-size: 1.8rem,
    line-height: 2.8rem,
  ),
  f16: (
    font-size: 1.6rem,
    line-height: 2.4rem,
  ),
  f12: (
    font-size: 1.2rem,
    line-height: 2rem,
  ),
  weight: (
    light: 300,
    regular: 400,
    semi-bold: 600,
    bold: 700,
  ),
);
