    margin-right: 24px;
  }
}
@media only screen and (max-width: 575.86px) {
  .blog .blog__card {
    max-width: 280px;
    margin-right: 16px;
  }
  .blog .blog__card:last-child {
    margin-right: 24px;
  }
}

